import {
  getChilds
} from './utils.js'
init()

function init() {
  setSearchEvent()
  setNewsBarEvent()
}

function setSearchEvent() {
  const ul = document.querySelector('#search')
  const childs = getChilds(ul.childNodes)
  for (let i = 0; i < childs.length; i++) {
    const child = childs[i]
    child.addEventListener('click', function (e) {
      this.style.zIndex = 100
      for (let j = 0; j < childs.length; j++) {
        if (i < j) {
          // 右边
          childs[j].style.zIndex = 10 - j
        } else if (i > j) {
          // 左边
          childs[j].style.zIndex = 10 + j
        }
      }
    })
  }
}

function setNewsBarEvent() {
  const ul = document.querySelector('#newsBar')
  const news = Array.from(document.querySelectorAll('.news .switch'))
  const nums = Array.from(document.querySelectorAll('.number li'))
  const childs = getChilds(ul.childNodes)
  for (let i = 0; i < childs.length; i++) {
    const child = childs[i]
    child.addEventListener('click', function (e) {
      if (news[i].className.search('active') === -1) {
        news[i].className += ' active'
      }
      let numsIndex = 0
      for (let k = 0; k < nums.length; k++) {
        if (nums[k].className.search('active') !== -1) {
          numsIndex = k
          break
        }
      }
      news[i].querySelector('.list-wrap').style.transform = `translateX(-${numsIndex * 550}px)`
      this.className = 'active'
      for (let j = 0; j < childs.length; j++) {
        if (i !== j) {
          news[j].className = news[j].className.replace('active', '')
          childs[j].className = ''
        }
      }
    })
  }
}